<template>
  <div class="ak-date-shortcut">
    <Description
      :option="option"
      @removeOption="removeOption"
    />

    <div class="form-group">
      <ExtentionRowItem
        v-for="(rowObj,index) in formData"
        :key="index"
        :row-index="index"
        :row-count="formData.length"
        show-operation-button
        @addRow="addRow(formData,{
          key: '',
          value: ''
        })"
        @removeRow="removeRow(formData, index)"
      >

        <div class="col">
          <Input
            v-model="rowObj.key"
            :placeholder="$t('extensionProperty.enterDateDesc')"
          />
        </div>
        <div class="col">
          <Input
            v-model="rowObj.value"
            :placeholder="$t('extensionProperty.enterValue')"
          />
        </div>
      </ExtentionRowItem>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import commonExtension from '../mixins/common-extension'
import ExtentionRowItem from '../extension-row-item';
import deepClone from '../../../utils/deepClone';

const ROW_ITEM = {
  key: '',
  value: ''
}

export default {
  name: 'DateShortcut',

  mixins: [commonExtension],

  components: {
    ExtentionRowItem
  },

  data() {
    return {
      formData: [deepClone(ROW_ITEM)]
    }
  },

  watch: {
    formData: {
      handler(newValue) {
        const newFormData = this.filterForm(newValue);
        if (newFormData.length === 0) {
          this.$emit('dataChange', { key: this.option.key, value: '' });
        } else {
          this.$emit('dataChange', { key: this.option.key, value: newFormData });
        }
      },
      deep: true
    },
  },

  methods: {
    filterForm(originData) {
      const data = deepClone(originData)
      for (let i = data.length - 1; i >= 0; i--) {
        const item = data[i]
        if (!item.key || !item.value) {
          data.splice(i, 1)
        }
      }
      return data
    },

    init() {
      if (!this.defaultData) { 
        return
      }
      this.formData = deepClone(this.defaultData)
    }
  },

  created() {
    this.init()
  }
}
</script>

<style lang="scss" scoped>
@import url('../../../assets/css/common/extension-attr.less');
</style>